@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400&display=swap);
:root {
    --darkAccent: rgba(2, 0, 36, 1);
    --lightAccent: #f3d6e2;
}

* {
    box-sizing: border-box;
}

*,
html {
    line-height: 1em;
    padding: 0;
    margin: 0;
    font-family: 'Libre Franklin', sans-serif;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h3 {
    font-size: 2.6em;
    text-align: center;
    padding: 1em;
    font-weight: 500;
}
.linkCV a {
    color: #FFFFFF;
    text-decoration: none;
}

nav {
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    z-index: 10;
    top: 0;
    padding: 1.2em 0;
    color: white;
    background-color: var(--darkAccent);
    max-height: 4em;
    justify-content: center;
}

.navSubElement {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

nav span {
    font-size: 18px;
    border-bottom: 1px solid transparent;
    transition: border-bottom 0.2s ease;
    margin: 1em;
    padding-bottom: 0.3em;
}

nav span:hover {
    cursor: pointer;
    border-bottom: 1px solid white;
}

nav span:hover i {
    color: gold;
}

nav span.activeClass i {
    color: gold;
}

nav span.activeClass {
    border-bottom-color: var(--lightAccent);
}
.linksTab {
    text-align: center;
    display: inline-block;
}
.linksTab span {
    margin: 0 5px;
}

section {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1200px;
    border-bottom: 1px solid lightgrey;

    padding-top: 100px;
    padding-bottom: 100px;
    margin-left: auto;
    margin-right: auto;

    text-align: justify;

    /* background-color: green; */
}

section:nth-child(even) {
    flex-direction: row-reverse;
}

section img,
section div {
    height: auto;
    width: 100%;
}

section img {
    width: 50%;
}

section div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 1em;
}

@media screen and (max-width: 900px) {
    section,
    section:nth-child(even) {
        flex-direction: column;
    }

    section img {
        height: auto;
        width: auto;
        max-width: 100vw;
    }
}
.appContainer {
  height: auto;
  margin: 0;
  padding: 0;
  justify-content: center;
  align-items: center;

  /* background-color: red; */
}
